import { getProfile, followUser, unFollowUser } from '@/api/profile'
import { getArticles } from '@/api/article'
import ArticleList from '@/components/article-list'

export default {
  name: 'ProfileIndex',
  middleware: 'authenticated',
  components: {
    ArticleList
  },
  data() {
    return {
      tab: 'author',
      profile: {},
      articles: []
    }
  },
  // async asyncData({ params }) {
  //   const username = params.username
  //   try {
  //     const { data } = await getProfile(username)
  //     return {
  //       profile: data.profile
  //     }
  //   } catch (err) {
  //     console.log(err)
  //   }

  //   return {
  //     profile: {}
  //   }
  // },
  watch: {
    '$route.query': {
      handler(val) {
        const { tab } = val
        this.tab = tab
        this.loadArticles()
      },
      immediate: true
    }
  },
  created() {
    this.loadProfile()
    this.$router.push({
      name: 'profile',
      query: {
        tab: this.tab
      }
    })
  },
  methods: {
    async loadProfile() {
      const username = this.$route.params.username
      try {
        const { data } = await getProfile(username)
        this.profile = data.profile
      } catch (err) {
        console.log(err)
      }
    },
    async loadArticles() {
      const { tab } = this.$route.query
      if (!tab) {
        return
      }

      const username = this.$route.username
      const { data } = await getArticles({
        [tab]: username
      })
      this.articles = data.articles
    },
    async handleFollow(following) {
      const username = this.profile.username
      if (!username) {
        return
      }

      const operateUser = following ? followUser : unFollowUser
      const { data } = await operateUser(username)
      profile.following = !profile.following
    }
  }
}
